@font-face {
    font-family: "Belisa Plumilla Manual";
    src: url("../Fonts/belisa_plumilla.ttf") format('truetype');
}

@font-face {
    font-family: "Stint Ultra Expanded";
    src: url("../Fonts/StintUltraExpanded-Regular.ttf") format('truetype');
}

:root {
    --bg-of-div: rgba(255, 255, 255, 0.1);
    --bg-of-description: rgba(255, 255, 255, 0.25);
    --bg-of-creditsButton: rgba(255, 255, 255, 0.5);
    --bg-of-creditsButton_hover: rgba(255, 255, 255, 0.3);
    --backdrop-blur: blur(5px);
    --width-of-main: 82vw;
}

body {
    background-image: url("../background-image.jpg");
    background-size: cover;
    max-height: 100vh;
}

.containerMain {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main {
    position: absolute;
    background-color: var(--bg-of-div);
    backdrop-filter: var(--backdrop-blur);
    width: var(--width-of-main);
    height: 90vh;
    top: 5vh;
    border-radius: 20px;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: "Belisa Plumilla Manual";
    font-size: 20px;
    position: relative;
    width: 73vw;
    top: 10vh;
    left: 4vw;
}

.title h2 {
    font-size: 40px;
    color: rgb(21, 21, 21);
    margin-bottom: 0px;
}

.title h1 {
    font-size: 85px;
}

.description {
    position: absolute;
    font-family: "Stint Ultra Expanded";
    font-size: 18px;
    left: 50%;
    transform: translateX(-50%) translateY(12vh);
    padding: 15px;
    background-color: var(--bg-of-description);
    width: 60vw;
    height: 30vh;
    border-radius: 20px;
}

.allPages {
    position: absolute;
    display: flex;
    align-items: center;
    width: var(--width-of-main);
    height: 40px;
    top: 1vh;
    z-index: 1;
    border-bottom: 2px solid black
}

.allPages ul {
    list-style: none;
    display: flex;
    flex-direction: row;
}

.allPages ul li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3px;
    background-color: var(--bg-of-creditsButton);
    border-radius: 12px;
    border: 3px solid black;
    width: 5.3vw;
    height: 25px;
    font-size: 20px;
}

.allPages ul li:hover {
    background-color: var(--bg-of-creditsButton_hover);
    border: 2px solid black;
    cursor: pointer;
}

.allPages ul li a {
    color: black;
    font-family: "Stint Ultra Expanded";
    font-size: 19px;
    text-decoration: none;
    margin: 5px;
}
